<template>
  <div id="dv-full-screen-container" :ref="ref">
    <template v-if="ready">
      <slot></slot>
    </template>
  </div>
</template>

<script>
import autoResize from '../../../mixin/autoResize.js'

export default {
  name: 'DvFullScreenContainer',
  mixins: [autoResize],
  data () {
    return {
      ref: 'full-screen-container',
      allWidth: 0,
      scale: 0,
      datavRoot: '',
      ready: false
    }
  },
  methods: {
    afterAutoResizeMixinInit () {
      const { initConfig, setAppScale } = this

      initConfig()

      setAppScale()

      this.ready = true
    },
    initConfig () {
      const { dom } = this
      const { width, height } = screen
	  //保持宽屏
	  if(width>height){
		  this.allWidth = width		  		
		  dom.style.width = `${width}px`
		  dom.style.height = `${height}px`
	  }else{
		  this.allWidth = height
		  dom.style.width = `${height}px`
		  dom.style.height = `${width}px`
	  }      
    },
    setAppScale () {
      const { allWidth, dom } = this

      const currentWidth = document.body.clientWidth
	  const currentHeight = document.body.clientHeight
	  if(currentWidth>currentHeight){
		  dom.style.transform = `scale(${currentWidth / allWidth})`
	  }else{
		  dom.style.transform = `scale(${currentHeight / allWidth})`
	  }
	  //console.log("currentHeight:"+currentHeight);
	  //console.log("allWidth:"+allWidth);
      //dom.style.transform = `scale(${currentWidth / allWidth})`
    },
    onResize () {
      const { setAppScale } = this

      setAppScale()
    }
  }
}
</script>